<template>
    <el-table
            :data="tableData"
            style="width: 100%;">
        <el-table-column
                prop="time"
                label="时间"
                width="180">
        </el-table-column>
        <el-table-column
                prop="oderNum"
                label="订单号"
                width="180">
        </el-table-column>
        <el-table-column
                prop="inflow"
                label="资金流入"
                width="180">
        </el-table-column>
        <el-table-column
                prop="balance"
                label="钱包余额"
                width="180">
        </el-table-column>
        <el-table-column
                prop="moneyTo"
                label="资金转向">
        </el-table-column>
    </el-table>
</template>

<script>
    export default {
        data() {
            return {
                tableData: [{
                    time: this.$store.state.AccTable.time,
                    oderNum: this.$store.state.AccTable.oderNum,
                    inflow: this.$store.state.AccTable.inflow,
                    balance:this.$store.state.AccTable.balance,
                    moneyTo:this.$store.state.AccTable.moneyTo
                }]
            }
        },
        mounted(){
            this.$store.dispatch('getActionTable',
                this.tableData.time = this.$store.state.AccTable.time
            )
        },
    }
</script>